<template>
	<view>
		<view class="scroll-view-container">
			<!-- 左侧滑动区域 -->
			<scroll-view class="left-scroll-view" scroll-y="true" :style="{height: wh + 'px'}">
				<block v-for="(item, index) in cateList" :key="index">
					<view :class="['left-scroll-view-item', 
						index === active ? 'active' : '']" @click="acttiveChanged(index)"
					>{{ item.cat_name }}</view>
				</block>
			</scroll-view>
			<!-- 右侧滑动区域 -->
			<scroll-view scroll-y="true" :style="{height: wh + 'px'}">
				<view class="cate-lv2" v-for="(item2, index2) in cateLevel2" :key="index2">
					<!-- 2级分类标题 -->
					<view class="cate-lv2-title">/{{ item2.cat_name }}/</view>
					<!-- 3级分类内容 -->
					<view class="cate-item-list">
						<view class="cate-lv3-content" v-for="(item3, index3) in item2.children" :key="index3" @click="gotoGoodsList(item3)">
							<image :src="item3.cat_icon" mode=""></image>
							<text>{{ item3.cat_name }}</text>
						</view>
					</view>
				</view>
			</scroll-view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				wh: 0,
				cateList: [],
				active: 0,
				cateLevel2: [],
			};
		},
		onLoad() {
			const sysInfo = uni.getSystemInfoSync()
			this.wh = sysInfo.windowHeight
			
			this.getCateList()
		},
		methods: {
			async getCateList() {
				const { data: res } = await uni.$http.get('/api/public/v1/categories')
				if (res.meta.status !== 200) return uni.$showMsg()
				this.cateList = res.message
				this.cateLevel2 = res.message[0].children
			},
			acttiveChanged(index) {
				this.active = index
				// 重新为2级分类
				this.cateLevel2 = this.cateList[index].children
			},
			
			// 跳转到商品别表页面
			async gotoGoodsList(item3) {
				uni.navigateTo({
					url: '/subpkg/goods_list/goods_list?cid' + item3.cat_id
				})
			}
		}
	}
</script>

<style lang="scss">
	.scroll-view-container {
		display: flex;
		.left-scroll-view {
			width: 120px;
			.left-scroll-view-item {
				position: relative;
				background-color: #f7f7f7;
				line-height: 60px;
				text-align: center;
				font-size: 12px;
				&.active {
					background-color: #fff;
					&::before {
						position: absolute;
						top: 50%;
						left: 0;
						transform: translateY(-50%);
						content: ' ';
						display: block;
						width: 2px;
						height: 30px;
						background-color: red;
					}
				}
			}
		}
		.cate-lv2-title {
			font-size: 12px;
			text-align: center;
			font-weight: bold;
			padding: 15px 0;
		}
		
		.cate-item-list {
			display: flex;
			flex-wrap: wrap;
			
			.cate-lv3-content {
				width: 33.33%;
				display: flex;
				flex-direction: column;
				justify-content: center;
				align-items: center;
				margin-bottom: 10px;
				
				image {
					width: 60px;
					height: 60px;
				}
				text {
					font-size: 12px;
				}
			}
		}
	}
</style>
